  border: 1px solid #dce3e8;
  background: #fff;
}
.chat__row {
  display: grid;
  grid-template-columns: 313px 1fr;
  grid-template-rows: 500px;
}
.chat__nav {
  border-right: 1px solid #dce3e8;
}
.chat__search, .chat__title {
  border-bottom: 1px solid #dce3e8;
  height: 58px;
  display: flex;
  align-items: center;
}
.chat__search {
  padding: 5px 15px;
}
.chat__search-inp {
  border-radius: 20px;
  background: #f2f5fa;
  display: block;
  width: 100%;
  height: 35px;
  padding: 0 10px;
}
.chat__title {
  color: #292c32;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.33;
  padding: 10px 19px;
  flex: 0 0 auto;
}
.chat__body {
  position: relative;
  display: flex;
  flex-direction: column;
}
.chat__messages-wrap {
  flex: 1 1 auto;
  padding: 26px 20px 30px;
  margin: 0 0 50px;
  overflow: auto;
}
.chat__messages-wrap::-webkit-scrollbar {
  width: 5px;
}
.chat__messages-wrap::-webkit-scrollbar-track {
  background: #fff;
}
.chat__messages-wrap::-webkit-scrollbar-thumb {
  background: #f04973;
  border-radius: 5px;
  border: 1px solid #fff;
}
.chat__bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #dce3e8;
  display: flex;
}
.chat__mess-inp {
  display: block;
  width: 100%;
  height: 50px;
  font-size: 13px;
  line-height: 1.8;
  font-weight: 500;
  padding: 0 30px;
}
.chat__send-btn {
  background: #f04973;
  flex: 0 0 50px;
  height: 50px;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
}

.group-item {
  display: flex;
  align-items: center;
  padding: 13px 15px;
  border-bottom: 1px solid #dce3e8;
  cursor: pointer;
}
.group-item:hover {
  background: rgba(220, 227, 232, 0.1);
}
.group-item__image-wrap {
  flex: 0 0 32px;
  height: 32px;
  border-radius: 50%;
  overflow: hidden;
  margin: 10px;
}
.group-item__image {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
}
.group-item__name {
  color: #292c32;
  font-size: 12px;
  font-weight: 700;
  line-height: 19px;
}
.group-item__text {
  font-size: 11px;
  font-weight: 500;
  width: 195px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.mess-chat {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin: 0 0 20px;
}
.mess-chat:last-child {
  margin: 0;
}
.mess-chat.reverse {
  flex-direction: row-reverse;
}
.mess-chat.reverse .mess-chat__text {
  border-radius: 0 15px 15px 15px;
  background: #F2F5FA;
}
.mess-chat__text {
  border-radius: 15px 0 15px 15px;
  background: #fde4ea;
  font-size: 13px;
  font-weight: 500;
  line-height: 20px;
  padding: 18px 20px;
}
.mess-chat__image-wrap {
  flex: 0 0 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
}
.mess-chat__image {
  -o-object-fit: cover;
     object-fit: cover;
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1;
}`, "",{"version":3,"sources":["webpack://./src/components/pages/Account/ChatRoom/ChatRoom.scss"],"names":[],"mappings":"AAAA;EACI,yBAAA;EACA,gBAAA;AACJ;AAAI;EACI,aAAA;EACA,gCAAA;EACA,yBAAA;AAER;AAAI;EACI,+BAAA;AAER;AAAI;EAEI,gCAAA;EACA,YAAA;EACA,aAAA;EACA,mBAAA;AACR;AACI;EACI,iBAAA;AACR;AACI;EACI,mBAAA;EACA,mBAAA;EACA,cAAA;EACA,WAAA;EACA,YAAA;EACA,eAAA;AACR;AACI;EACI,cAAA;EACA,eAAA;EACA,gBAAA;EACA,iBAAA;EACA,kBAAA;EACA,cAAA;AACR;AACI;EACI,kBAAA;EACA,aAAA;EACA,sBAAA;AACR;AACI;EACI,cAAA;EACA,uBAAA;EACA,gBAAA;EACA,cAAA;AACR;AAAQ;EACI,UAAA;AAEZ;AAAQ;EACI,gBAAA;AAEZ;AAAQ;EACI,mBAAA;EACA,kBAAA;EACA,sBAAA;AAEZ;AACI;EACI,kBAAA;EACA,SAAA;EACA,OAAA;EACA,WAAA;EACA,6BAAA;EACA,aAAA;AACR;AACI;EACI,cAAA;EACA,WAAA;EACA,YAAA;EACA,eAAA;EACA,gBAAA;EACA,gBAAA;EACA,eAAA;AACR;AACI;EACI,mBAAA;EACA,cAAA;EACA,YAAA;EACA,WAAA;EACA,eAAA;EACA,gBAAA;AACR;;AAGA;EACI,aAAA;EACA,mBAAA;EACA,kBAAA;EACA,gCAAA;EACA,eAAA;AAAJ;AACI;EACI,oCAAA;AACR;AACI;EACI,cAAA;EACA,YAAA;EACA,kBAAA;EACA,gBAAA;EACA,YAAA;AACR;AACI;EACI,oBAAA;KAAA,iBAAA;EACA,WAAA;EACA,YAAA;AACR;AACI;EACI,cAAA;EACA,eAAA;EACA,gBAAA;EACA,iBAAA;AACR;AACI;EACI,eAAA;EACA,gBAAA;EACA,YAAA;EACA,mBAAA;EACA,uBAAA;EACA,gBAAA;AACR;;AAGA;EACI,aAAA;EACA,yBAAA;EACA,SAAA;EACA,gBAAA;AAAJ;AACI;EACI,SAAA;AACR;AACI;EACI,2BAAA;AACR;AAAQ;EACI,+BAAA;EACA,mBAAA;AAEZ;AACI;EACI,+BAAA;EACA,mBAAA;EACA,eAAA;EACA,gBAAA;EACA,iBAAA;EACA,kBAAA;AACR;AACI;EACI,cAAA;EACA,YAAA;EACA,kBAAA;EACA,gBAAA;AACR;AACI;EACI,oBAAA;KAAA,iBAAA;EACA,WAAA;EACA,YAAA;EACA,iBAAA;AACR","sourcesContent":[".chat {\n    border: 1px solid #dce3e8;\n    background: #fff;\n    &__row {\n        display: grid;\n        grid-template-columns: 313px 1fr;\n        grid-template-rows: 500px;\n    }\n    &__nav {\n        border-right: 1px solid #dce3e8;\n    }\n    &__search,\n    &__title {\n        border-bottom: 1px solid #dce3e8;\n        height: 58px;\n        display: flex;\n        align-items: center;\n    }\n    &__search {\n        padding: 5px 15px;\n    }\n    &__search-inp {\n        border-rad